body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Cell {
    display: -webkit-flex;
    display: flex;
    cursor: pointer;
    width: 32%;
    height: 32%;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}
.Board {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-content: space-between;
            align-content: space-between;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    background-color: yellowgreen;
    width: 20rem;
    height: 20rem;
    -webkit-align-self: center;
            align-self: center;
    border: solid;
    border-color: yellowgreen;
}

.overlay {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 10;
    background-color: rgba(0,0,0,0.5);
  }

  .overoverlay {
    display: -webkit-flex;
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    -webkit-flex-direction: row;
            flex-direction: row;
    height: 20%;
    background-color: rgba(0,0,0,0.75);
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
  }

  .winnerText {
      color: white;
      font-size: 2em;
      margin: 1em;
  }

  .winnerIcon {      
      height: 80%;
      width: auto;
  }
.Score {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    padding: 1rem;
    border-radius: 1em;
    margin: 1em;
}

.Icon {
    width: 2em;
    height: auto;
}
.CustomButton {
    width: 100%;
    padding: 1rem;    
    border-radius: 1em;
    margin: 1em;
}
.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  text-align: center;
  background-color: blue;
  background-image: url(/react-tic-tac-toe/static/media/wall.36488e54.jpg);
  background-size: 100%;
}

.ScoreContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%
}

.ActivePlayer {

}

.ButtonsContainer {
  display: -webkit-flex;
  display: flex;
  -webkit-flex: 1 1;
          flex: 1 1;
  -webkit-flex-direction: row;
          flex-direction: row;
}
